@import url('https://fonts.googleapis.com/css2?family=Noto+Sans&display=swap');
.hover-div {
    width: 200px;
    height: 200px;
    display: block;
    z-index: 1000;
    color: pink;
}

body {
    color: #F7EDE2;
}

title {
    background-color: blue;
    width: 200px;
    height: 200px;
}

body {
    display: flex;
    flex-direction: column;
    background-color:#9A9898;
}

#title-div {
    color: #F7EDE2; 
    width: 100%;
    text-align: center;
    margin-top: 5px; 
    margin-bottom: 5px;
}

#orientation {
    display: flex;
    
}

#map-container {
    margin-left: 15px;
}

#state-holder {
    margin-left: 15px;
}

#instruction {
    text-align: center;
    width: 93%;
    margin-left: 3.5%;
    margin-right: 3.5%;
    font-size: medium;
}

body {
    font-family: 'Noto Sans', sans-serif;
}

select {
    font-family: 'Noto Sans', sans-serif;
    background-color: #9A9898;
    border: 1px solid #F28482;
    color: #F7EDE2;
}

th {
    font-family: 'Noto Sans', sans-serif;
}

td {
    font-family: 'Noto Sans', sans-serif;
}

#nav-bar { 
    display: flex;
    
    align-items: baseline;
    background-color: #FF7E6B;
}

img {
    height: 30px;
    width: 30px;
    margin-right: 10%;
}

#links {
    display: flex;
    justify-content: space-between;
}

#links > a {
    width: 30%;
}

#chart-container {
    display: flex;
    justify-content: center;
}

#footer {
    display: flex;
    justify-content: space-around;
    margin-top: 4%;
    align-items: baseline;
}

#legend {
    display: flex; 
    align-items: baseline;
}

#legend-cont {
    border: 1px solid #031110;
    padding: 5px;
    border-radius: 8px;
}

select {
    border-radius: 5px;
}

tbody {
    margin: 0;
}

td {
    text-align: center;
    background-color: #D6D6D6; 
    color: #FF7E6B;
}

.tick {
    width: 120px;
}

#dropdown-cont2 {
    display: flex;
    align-items: baseline;
}

option {
    border-right: 1px solid #FF7E6B;
    border-left: 1px solid #FF7E6B;
}

#map-div {
    zoom: 0.75;
  
}

#data-table {
    zoom: 0.75;
  
}

#risk-data-table {
    zoom: 0.75;
  
}

#state-holder {
    display: flex; 
    align-items: center;
    flex-direction: column;
    width: 100%;
}